___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 1024px) {
  .menu li {
    margin-right: 5px;
  }
}

@media screen and (max-width: 768px) {
  .navbar .container .menu {
    display: none;
  }

  .navbar .container .menu.aktif {
    display: block;
    position: absolute;
    top: 64px;
    background: #008080;
    padding: 10px 20px;
    right: 0;
    left: 0;
  }

  .navbar .container .menu.aktif li a {
    padding: 10px;
    display: block !important;
  }

  .tombol {
    display: block;
  }

  .welcome-content {
    display: flex;
    flex-direction: column;
  }

  /* Footer responsive styles for 768px */
  .footer-top {
    flex-direction: column;
    align-items: center;
  }
  .footer-left, .footer-center, .footer-right {
    min-width: 100%;
    text-align: center;
  }

  .footer-right .about-title h3 {
    margin-bottom: 10px;
    margin-top: 20px;
  }

  .footer-left img {
    width: 100%;
    max-width: 300px;
    height: auto;
  }
  .footer-links {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer-links li {
    margin: 10px 0;
  }
  .footer-icons {
    margin-top: 10px;
  }
}
