___CSS_LOADER_EXPORT___.push([module.id, `.add-article {
  max-width: 800px;
  margin: 0 auto;
  margin-top: 120px;
  display: flex;
  justify-content: center;
  padding: 2rem;
}

.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: #f8f5f5;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 32px;
  padding: 20px;
}

.form-container form {
  width: 100%;
  max-width: 800px;
}

.form-container h2 {
  text-align: center;
  margin-bottom: 24px;
  font-size: 24px;
  color: #333;
}

.form-component {
  display: grid;
  grid-template-columns: 1fr 9fr;
  align-items: baseline;
  justify-content: start;
  justify-items: center;
  margin-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  background-color: #fff;
}

.form-component i {
  margin-right: 10px;
  color: #888;
}

.form-component input,
.form-component textarea {
  width: 100%;
  border: none;
  outline: none;
  font-size: 12px;
  padding: 8px;
}

.form-component input::placeholder,
.form-component textarea::placeholder {
  color: #aaa;
}

textarea {
  resize: vertical;
  height: 500px;
}

.form-button {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.form-button button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}

.form-button button[type="submit"] {
  background-color: #28a745;
  color: #fff;
}

.form-button #clear {
  background-color: #dc3545;
  color: #fff;
}

/* Styling untuk CKEditor agar sesuai dengan form */
.ck-editor__editable {
  min-height: 500px;
  width: 100%;
  border: none;
  outline: none;
  font-size: 12px;
  padding: 8px;
  background-color: #fff;
}

.ck.ck-toolbar {
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.ck.ck-toolbar .ck-button.ck-on {
  color: #0056b3;
}

.ck.ck-editor__editable_inline {
  font-family: "Poppins" sans-serif;
  font-size: 12px;
  line-height: 1.6;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
}
