@media screen and (max-width: 1024px) {
  .content-image img {
    max-width: 400px;
  }
}

@media screen and (max-width: 768px) {
  body {
    box-sizing: border-box;
  }

  .content {
    width: 100%;
  }

  .content h1 {
    font-size: 23px;
  }

  .content p {
    text-align: justify;
  }

  .content-description .title {
    font-size: 27px;
  }

  .content-description p {
    font-size: 15px;
    text-align: center;
  }

  .content-description a {
    padding: 0.7rem 2rem;
  }

  .content-description a i {
    font-size: 10px;
  }

  .benefit-conclusion p {
    margin-top: 10px;
    text-align: center;
  }

  .content-image img {
    max-width: 350px;
    margin-top: 70px;
  }

  .content-box img {
    width: 250px;
    margin: 10px;
  }

  .card-container .card img {
    padding-right: 15px;
  }

  .card-container .card .container h3 {
    font-size: 13px;
  }

  .benefits-content .card-benefit img {
    max-width: 130px;
  }

  .benefits-content .card-benefit h3 {
    font-size: 15px;
  }

  .welcome-content {
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
    margin-bottom: 50px;
    justify-content: space-evenly;
    min-height: 50vh;
  }

  .welcome-content .content-image {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
  }

  .disease-title h3 {
    font-size: 18px;
  }

  .disease-body p {
    font-size: 15px;
    text-align: justify;
  }

  .benefits-content .benefits-container {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
  }

  .cigarette-content .content-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .cigarette-content .content-box img {
    max-width: 300px;
  }

  .smoking-disease {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .smoking-disease .disease-container {
    grid-template-columns: 1fr;
    max-width: 600px;
  }

  .smoking-disease .disease-container .disease-body {
    padding: 0;
  }

  .comments-container .commentbox,
  .comment-list {
    flex-direction: column;
  }

  .commentbox img {
    width: 70px;
  }

  .form-content h2 {
    font-size: 17px;
  }

  .comment-form textarea {
    max-height: 100px;
  }

  .comment-form button {
    border-radius: 5px;
    padding: 8px 20px;
    font-size: 1rem;
    cursor: pointer;
  }

  .comment-list img {
    width: 70px;
  }

  .comment-item p {
    font-size: 13px;
    text-align: left;
  }
}

@media screen and (max-width: 480px) {
  body {
    box-sizing: border-box;
  }

  .content {
    width: 100%;
  }

  .content h1 {
    font-size: 20px;
  }

  .content-image img {
    max-width: 320px;
  }

  .content-box img {
    width: 250px;
    margin: 10px;
  }

  .disease-title {
    margin: 5px 20px;
  }

  .disease-title h3 {
    font-size: 17px;
  }

  .disease-body p {
    font-size: 13px;
    text-align: justify;
    margin: 10px 20px;
  }

  .content-description .title {
    font-size: 27px;
  }

  .content-description p {
    font-size: 12.5px;
    text-align: center;
  }

  .content-description a {
    padding: 0.7rem 2rem;
  }

  .content-description a i {
    font-size: 10px;
  }

  .content-image img {
    margin-top: 50px;
  }

  .benefits-content .card-benefit {
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 20px;
  }

  .benefits-content .card-benefit img {
    max-width: 130px;
  }

  .benefits-content .card-benefit h3 {
    font-size: 15px;
  }

  .benefit-conclusion h3 {
    font-size: 17px;
    margin-bottom: 10px;
  }

  .benefit-conclusion p {
    font-size: 14px;
  }

  .commentbox img {
    width: 70px;
  }

  .form-content h2 {
    font-size: 17px;
  }

  .comment-form textarea {
    max-height: 100px;
  }

  .comment-form button {
    border-radius: 5px;
    padding: 8px 20px;
    font-size: 1rem;
    cursor: pointer;
  }

  .comment-list img {
    width: 70px;
  }

  .comment-item p {
    font-size: 13px;
    text-align: left;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/home-responsive.css"],"names":[],"mappings":"AAAA,qCAAqC;AACrC;EACE;IACE,gBAAgB;EAClB;AACF;;AAEA;EACE;IACE,sBAAsB;EACxB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,mBAAmB;EACrB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;IACf,kBAAkB;EACpB;;EAEA;IACE,oBAAoB;EACtB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,gBAAgB;IAChB,kBAAkB;EACpB;;EAEA;IACE,gBAAgB;IAChB,gBAAgB;EAClB;;EAEA;IACE,YAAY;IACZ,YAAY;EACd;;EAEA;IACE,mBAAmB;EACrB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,8BAA8B;IAC9B,kBAAkB;IAClB,mBAAmB;IACnB,6BAA6B;IAC7B,gBAAgB;EAClB;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;EACf;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;IACf,mBAAmB;EACrB;;EAEA;IACE,aAAa;IACb,uBAAuB;IACvB,SAAS;IACT,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;EACzB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;EACrB;;EAEA;IACE,0BAA0B;IAC1B,gBAAgB;EAClB;;EAEA;IACE,UAAU;EACZ;;EAEA;;IAEE,sBAAsB;EACxB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;IACf,eAAe;EACjB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,eAAe;IACf,gBAAgB;EAClB;AACF;;AAEA;EACE;IACE,sBAAsB;EACxB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,YAAY;IACZ,YAAY;EACd;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;IACf,mBAAmB;IACnB,iBAAiB;EACnB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,iBAAiB;IACjB,kBAAkB;EACpB;;EAEA;IACE,oBAAoB;EACtB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,oBAAoB;IACpB,kBAAkB;IAClB,mBAAmB;IACnB,mBAAmB;EACrB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;IACf,mBAAmB;EACrB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;IACf,eAAe;EACjB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,eAAe;IACf,gBAAgB;EAClB;AACF","sourcesContent":["/* Media Queries for Responsiveness */\r\n@media screen and (max-width: 10